import * as types from './../constants/ActionType';

var data = JSON.parse(localStorage.getItem('cart'));

var initialState = data ? data : [];

const cart = (state = initialState, action) => {
    switch(action.type){
        case types.ADD_TO_CART: {
            let index = findProductInState(state, action.product);
            if( index === -1 ){
                state.push({
                    product : action.product,
                    quantity : action.quantity
                });
            }
            else{
                state[index] = {
                    ...state[index],
                    quantity: state[index].quantity + 1

                }
            }
            updateToLocalStorage(state);
            return [...state];
        }
        case types.DELETE_CART_ITEM: {
            let index = findProductInState(state, action.product);
            if( index !== -1 ){
                state.splice(index,1);
                updateToLocalStorage(state);
            }
            return [...state];
        }
        case types.CHANGE_CART_ITEM_QUANTITY: {
            let index = findProductInState(state, action.product);
            if( index !== -1 && state[index].quantity > 0){
                if( !(state[index].quantity === 1 && action.amount === -1))
                state[index] = {
                    ...state[index],
                    quantity: state[index].quantity + action.amount

                }
            }
            return [...state];
        }
        default: return [...state];
    }
}

var updateToLocalStorage = (state) => {
    localStorage.setItem('cart',JSON.stringify(state));
}

var findProductInState = (state, product) => {
    let idx = -1;
    
    state.forEach((item, index) => {
        if( item.product.id === product.id ){
            idx = index;
            return;
        }
    });

    return idx;
}

export default cart;