import React, { Component } from "react";
import {connect} from 'react-redux';
import * as messages from './../constants/Message';
import * as actions from './../actions/index';

class CartItem extends Component {
    render() {
        var { item } = this.props;
        return (
                <tr>
                    <th scope="row">
                        <img src={ item.product.image }
                            alt={ item.product.name } className="img-fluid z-depth-0" />
                    </th>
                    <td>
                        <h5>
                            <strong>{ item.product.name }</strong>
                        </h5>
                    </td>
                    <td>{ item.product.price }$</td>
                    <td className="center-on-small-only">
                        <span className="qty">{ item.quantity } </span>
                        <div className="btn-group radio-group" data-toggle="buttons">
                            <label className="btn btn-sm btn-primary btn-rounded waves-effect waves-light"
                                onClick={ () => this.onChangeQuantity(item.product, -1) }
                            >
                                <a>—</a>
                            </label>
                            <label className="btn btn-sm btn-primary btn-rounded waves-effect waves-light"
                                onClick={ () => this.onChangeQuantity(item.product, 1) }
                            >
                                <a>+</a>
                            </label>
                        </div>
                    </td>
                    <td>{ this.showTotalAmount(item.product.price, item.quantity) }$</td>
                    <td>
                        <button type="button" className="btn btn-sm btn-primary waves-effect waves-light" data-toggle="tooltip" data-placement="top"
                            title="" data-original-title="Remove item"
                            onClick={ () => this.onDeleteCartItem(item.product) }    
                        >
                            X
                        </button>
                    </td>
                </tr>
        );
    }

    onChangeQuantity = (product, amount) => {
        this.props.onChangeQuantity(product, amount);
        this.props.onChangeMessage(messages.MSG_UPDATE_CART_SUCCESS);
    }

    onDeleteCartItem = (product) => {
        this.props.onDeleteCartItem(product);
        this.props.onChangeMessage(messages.MSG_DELETE_PRODUCT_IN_CART_SUCCESS);
    }

    showTotalAmount = (price, quantity) => {
        return price * quantity;
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {
        onDeleteCartItem: (index) => {
            dispatch(actions.atcDeleteCartItem(index));
        },
        onChangeMessage: (message) => {
            dispatch(actions.atcChangeMessage(message));
        },
        onChangeQuantity: (product, amount) => {
            dispatch(actions.atcOnChangeQuantity(product, amount));
        }
    };
}

export default connect(null, mapDispatchToProps)(CartItem);
