import * as types from './../constants/ActionType';

export const atcAddToCart = (product) => {
    return {
        type : types.ADD_TO_CART,
        product,
        quantity : 1
    }
}

export const atcChangeMessage = (message) => {
    return {
        type: types.CHANGE_MESSAGE,
        message
    }
}

export const atcDeleteCartItem = (product) => {
    return {
        type: types.DELETE_CART_ITEM,
        product
    }
}

export const atcOnChangeQuantity = (product, amount) => {
    return {
        type: types.CHANGE_CART_ITEM_QUANTITY,
        product,
        amount
    }
}


